.container {
  padding-left: 15px;
  padding-right: 15px;
  min-width: 320px;
  margin: 0 auto;

  .header & {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  @media screen and (min-width: 768px) {
    width: 768px;
  }

  @media screen and (min-width: 1200px) {
    width: 1200px;
  }
}
